import React, { Suspense, lazy } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider as MuiThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { ThemeProvider, useTheme } from './contexts/ThemeContext';
import { QueryProvider } from './contexts/QueryContext';
import ErrorBoundary from './components/ErrorBoundary';
import LoadingSpinner from './components/common/LoadingSpinner';

// Auth components (needed immediately, no lazy loading)
import Login from './components/auth/Login';
import Register from './components/auth/Register';

// Layout (needed immediately)
import Layout from './components/layout/Layout';
import RequireRole from './components/RequireRole';

// Lazy loaded components for better performance
const Dashboard = lazy(() => import('./components/Dashboard'));
const NoteEditor = lazy(() => import('./components/notes/NoteEditor'));
const AdminPortal = lazy(() => import('./components/admin/AdminPortal'));
const PasswordManager = lazy(() => import('./components/passwords/PasswordManager'));
const Calendar = lazy(() => import('./components/calendar/Calendar'));
const Profile = lazy(() => import('./components/Profile'));
const ProjectList = lazy(() => import('./components/projects/ProjectList'));
const ProjectView = lazy(() => import('./components/projects/ProjectView'));
const ProjectForm = lazy(() => import('./components/projects/ProjectForm'));
const TaskView = lazy(() => import('./components/projects/TaskView'));

const AppContent = () => {
  const { darkMode } = useTheme();
  
  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: '#2196f3',
      },
      secondary: {
        main: '#f50057',
      },
    },
  });

  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      <QueryProvider>
        <AuthProvider>
          <Router>
            <ErrorBoundary>
              <Routes>
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              <Route 
                path="/admin" 
                element={
                  <RequireRole roles={["admin","developer"]}>
                    <PrivateRoute>
                      <Layout>
                        <Suspense fallback={<LoadingSpinner message="Loading Admin Portal..." />}>
                          <AdminPortal />
                        </Suspense>
                      </Layout>
                    </PrivateRoute>
                  </RequireRole>
                } 
              />
              <Route
                path="/"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Dashboard..." />}>
                        <Dashboard />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/notes/:id"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Note Editor..." />}>
                        <NoteEditor />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/passwords"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Password Manager..." />}>
                        <PasswordManager />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/calendar"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Calendar..." />}>
                        <Calendar />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/calendar/:date"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Calendar..." />}>
                        <Calendar />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/profile"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Profile..." />}>
                        <Profile />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/projects"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Projects..." />}>
                        <ProjectList />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/projects/new"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Project Form..." />}>
                        <ProjectForm />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/projects/:id"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Project..." />}>
                        <ProjectView />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/projects/:id/edit"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Project Form..." />}>
                        <ProjectForm />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
              <Route
                path="/projects/:projectId/tasks/:taskId"
                element={
                  <PrivateRoute>
                    <Layout>
                      <Suspense fallback={<LoadingSpinner message="Loading Task..." />}>
                        <TaskView />
                      </Suspense>
                    </Layout>
                  </PrivateRoute>
                }
              />
            </Routes>
          </ErrorBoundary>
        </Router>
      </AuthProvider>
      </QueryProvider>
    </MuiThemeProvider>
  );
};

const PrivateRoute = ({ children }) => {
  const { isAuthenticated } = useAuth();
  return isAuthenticated ? children : <Navigate to="/login" />;
};

function App() {
  return (
    <ThemeProvider>
      <AppContent />
    </ThemeProvider>
  );
}

export default App; 